/* HomePage.css */

.home-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #FFFAF1;
  font-family: 'Inter', sans-serif;
  color: #363636;
  overflow-y: auto;
  padding-bottom: 80px; /* Space for navbar */
}

.home-content {
  padding: 24px 16px;
}

/* Greeting Section */
.greeting-section {
  margin-bottom: 24px;
}

.greeting-title {
  font-size: 28px;
  font-weight: 700;
  margin: 0 0 4px;
  color: #363636;
}

.greeting-subtitle {
  font-size: 16px;
  margin: 0;
  color: #666;
}

/* Calendar Section */
.calendar-section {
  margin-bottom: 32px;
  overflow: hidden;
}

.calendar-header {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-align: center;
  font-size: 12px;
  color: #666;
  margin-bottom: 8px;
}

.calendar-days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
  text-align: center;
}

.calendar-day {
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  cursor: pointer;
  border-radius: 50%;
}

.calendar-day:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.calendar-day.today {
  background-color: #363636;
  color: white;
}

.calendar-day.empty {
  pointer-events: none;
}

/* Section Styling */
.section {
  margin-bottom: 36px;
}

.section-title {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 16px;
  color: #363636;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 28px 0;
}

.sad-face {
  margin-bottom: 16px;
}

.empty-text {
  font-size: 16px;
  color: #666;
  text-align: center;
  margin: 0;
}

/* Event Card */
.event-card {
  background-color: #FFE0EE;
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  cursor: pointer;
  transition: transform 0.2s;
}

.event-card:hover {
  transform: scale(1.01);
}

.event-card-content {
  flex: 1;
  padding: 16px;
}

.event-title {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 8px;
  color: #391E99;
}

.event-details {
  padding: 8px 0;
}

.event-date-time {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 4px;
  color: #391E99;
}

.event-host {
  font-size: 14px;
  color: #391E99;
}

.host-name {
  font-weight: 600;
}

.event-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  background-color: #FFB8DB;
  color: #363636;
}

/* Responsive adjustments */
@media (min-width: 768px) {
  .home-container {
    max-width: 500px;
    margin: 0 auto;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
  }
}